import ol from 'openlayers';
import FeatureWrapper from './FeatureWrapper';
import axios from 'axios';
require('es6-promise').polyfill();

// 初期設定
const env = {
  dataHost: '', // 同一ホストからデータを取得するなら空文字でOK
  locationDataFeatchIntervalSec: 5,
  selfLocationIconImage: 'img/self.png',
  defaultLocation: {
    lon: 142.3605716228485,
    lat: 43.765143524274045,
    zoom: 16 } };

// バスかあさっピーか(Querystringからindex.htmlで取得している)
const targetType = _targetType;

// ターゲットとなるデバイス情報を取得するURL
const devicesConfigUrl = `${env.dataHost}/data/${targetType}/config.json`;

// 位置情報が書き込まれたJSONののURL
const locationDataUrl = `${env.dataHost}/data/${targetType}/locations.json`;

// Openlayers Mapのインスタンス
let ol3Map;
// デバイス表示layer
let deviceLayer;
// Landmark表示layer
let landmarkLayer;
// ルートを表示するlayer()
let routeLayer;

// デバイスを表示するFeatureたち
// deviceFeaturesの各要素がMap上に表示されるアイコンになる。
// 対象はページロード時のconfig.devicesで決定する。
// 位置情報の供給が停止されたとき（サーバーから取得できないとき）は、この要素を削除するのではなく、
// 非表示に切り替える。FeatureWrapper#hide()/show()で切り替える
// DeviceIdをキーとした連想配列で持つ
// DeviceIdがキーなので、位置情報を取得したときにdeviceFeatures[DeviceId]でFeatureWrapper
// への参照を取得して、位置情報を与えることで、Featureが移動する。
const deviceFeatures = [];

// 自分の位置を表示するFeature
let selfLocationFeature;

// Mapの初期化
const prepareMap = () => {
  ol3Map = new ol.Map({
    target: 'ol3Map',
    renderer: ['canvas', 'dom'],
    layers: [
      new ol.layer.Tile({
        source: new ol.source.OSM })],
    controls: ol.control.defaults({
      attributionOptions: ({
        collapsible: false }),
      zoom: false }).extend([
      // new HomeButton()
      ]),
    view: new ol.View({
      projection: 'EPSG:3857',
      center: ol.proj.fromLonLat([
        env.defaultLocation.lon,
        env.defaultLocation.lat]),
      maxZoom: 18,
      zoom: env.defaultLocation.zoom }) });
  // 右寄せのZoom
  ol3Map.addControl(new ol.control.Zoom({ className: 'ol-zoom' }));
  // device表示用のレイヤーを追加
  deviceLayer = new ol.layer.Vector({
    source: new ol.source.Vector({
      features: [] }) });
  // landmark表示用のレイヤーを追加
  landmarkLayer = new ol.layer.Vector({
    source: new ol.source.Vector({
      features: [] }) });

  routeLayer = new ol.layer.Vector({
    source: new ol.source.Vector({
      features: [] }) });

  ol3Map.addLayer(routeLayer);
  ol3Map.addLayer(landmarkLayer);
  ol3Map.addLayer(deviceLayer);
};

// デバイス定義データを取得する
const fetchConfigData = (params) =>
  new Promise((resolve, reject) => {
    axios.get(
      `${devicesConfigUrl}?_dummy=${(new Date()).getTime()}`,
      { headers: { 'Content-Type': 'application.json' } }
    ).then((response) => {
      const result = params;
      result.config = response.data;
      resolve(result);
    }).catch((response) => {
      reject(response);
    });
  });

// ルートを表示する
// config にroutesが有る場合、それに基づいて
// MultiLineを描画する
const renderRouteLines = (config) => {
  if (!config.routes) return;

  // 経路の配列（ひとつの経路が二次元配列なので、複数経路で3次元配列となる
  const routes = [];
  // 要素を走査し、JSON（String）の緯度経度をNumberで再設定する
  config.routes.map((readRoute) => {
    const points = [];
    readRoute.map((readPoint) => {
      points.push([Number(readPoint[0]), Number(readPoint[1])]);
      return readPoint;
    });
    routes.push(points);
    return readRoute;
  });
  const lines = new ol.geom.MultiLineString([]);
  lines.setCoordinates(routes);
  // 座標系を変換する
  const feature = new ol.Feature(lines.transform('EPSG:4326', 'EPSG:3857'));
  const source = new ol.source.Vector({ features: [feature] });

  routeLayer.setSource(source);

  const style = new ol.style.Style({
    stroke: new ol.style.Stroke({
      color: 'green',
      width: 5 }) });
  routeLayer.setStyle(style);
  // routeLayer.setSource(source);
};


// Featureを生成して、Mapに配置する
// この時点では生成と配置のみで表示は行わない（緯度経度が取得されていないので
// 自分の位置を表示するFeatureもここで設定する
const putFeatures = (params) => {
  // Layerに渡すために、Featureの配列を作る。
  // Layerへの配置後の操作はdeviceFeatures経由で行うので、ここでの配列は
  // 一時的な変数である
  const tempArr = [];
  Object.keys(params.config.devices).map((key) => {
    deviceFeatures[key] = new FeatureWrapper(
          key,
          params.config.devices[key].groupId,
          env.defaultLocation.lon,
          env.defaultLocation.lat,
          params.config.devices[key].image);
    tempArr.push(deviceFeatures[key]);
    return key;
  });

  // 自分の表示位置を初期の中心位置で作成する（この時点では非表示のまま
  selfLocationFeature = new FeatureWrapper(
    'self',
    '',
    env.defaultLocation.lon,
    env.defaultLocation.lat,
    env.selfLocationIconImage);
  tempArr.push(selfLocationFeature);

  // レイヤーに表示
  deviceLayer.setSource(
    new ol.source.Vector({
      features: tempArr })
  );
  return Promise.resolve(params);
};

// 位置情報データを取得する
const fetchLocationData = (config) =>
  new Promise((resolve, reject) => {
    axios.get(
      `${locationDataUrl}?_dummy=${(new Date()).getTime()}`,
      { headers: { 'Content-Type': 'application.json' } }
    ).then((response) => {
      // 位置データが取得できたかどうか
      let hasLocationData = false;
      // configに定義されたdeviceIdのFeatureがgetした位置情報に存在していれば、
      // 緯度経度を設定してshow()
      // なければ、緯度経度は初期値にしてhide()
      Object.keys(config.devices).map((key) => {
        if (deviceFeatures[key]) {
          if (response.data[key]) {
            deviceFeatures[key].setLonlat({
              lon: Number(response.data[key].lon),
              lat: Number(response.data[key].lat) });
            hasLocationData = true;
            deviceFeatures[key].show();
          } else {
            deviceFeatures[key].hide();
            deviceFeatures[key].setLonlat({
              lon: env.defaultLocation.lon,
              lat: env.defaultLocation.lat });
          }
        }
        return key;
      });
      console.log(`fetchLocationData():${JSON.stringify(response.data)}`);
      // デバイスが発見できなければメッセージを表示
      setNoActiveDevice(!hasLocationData);
      resolve(response.data);
    }).catch((response) => {
      // alert(`status:${response.status}\n${response.statusText}`);
      // console.log(response);
      reject(response);
    });
  });

// Landmark(バス停など)を表示する
const putLandmarks = (params) => {
  const landmarkFeatures = [];
  if (params.config.landmarks) {
    Object.keys(params.config.landmarks).map((key) => {
      const feat = new FeatureWrapper(
            key,
            '',
            Number(params.config.landmarks[key].lon),
            Number(params.config.landmarks[key].lat),
            params.config.landmarks[key].image,
            params.config.landmarks[key].label);
      feat.show();
      landmarkFeatures.push(feat);
      return key;
    });
    // レイヤーに表示
    landmarkLayer.setSource(
      new ol.source.Vector({
        features: landmarkFeatures })
    );
  }
  return Promise.resolve();
};

// 自分の位置情報の取得を開始
const startWatchSelfLocation = () => {
  navigator.geolocation.watchPosition((position) => {
    selfLocationFeature.setLonlat({
      lon: position.coords.longitude,
      lat: position.coords.latitude });
    if (!selfLocationFeature.isShown()) {
      selfLocationFeature.show();
    }
    // console.log(`Self location : lon ${position.coords.longitude}/ `
    //   + `lat ${position.coords.latitude}`);
  });
};

// Mapの初期化
prepareMap();

// デバイス表示初期処理
// Promiseでわたっていくparamsは以下の定義
// {
//   config: fetchConfigData() で設定
// }
Promise.resolve({})
  .then((params) => fetchConfigData(params))
  .then((params) => {
    // landmarkの配置は非同期で投げっぱなし
    putLandmarks(params);
    return putFeatures(params);
  })
  .then((params) => {
    // putFeaturesが完了してから地震の位置情報の監視スレッドを開始する
    startWatchSelfLocation();
    fetchLocationData(params.config);
    // 経路を描画する
    renderRouteLines(params.config);
    // タイマー開始
    setInterval(() => fetchLocationData(params.config)
      , env.locationDataFeatchIntervalSec * 1000);
    return Promise.resolve(params);
  })
  .catch((error) => {
    console.error(error);
  });


// 自分の場所を中心にする
const toHome = () => {
  // GeolocationAPIを使用できない場合でも、初期値としてenvのlonlatを
  // 設定しているので、selfLocationFeatureから常に取得する
  const lonlat = selfLocationFeature.getLonlat();
  ol3Map.getView().setCenter(ol.proj.fromLonLat([
    lonlat.lon,
    lonlat.lat]));
  ol3Map.getView().setZoom(env.defaultLocation.zoom);
};

// デバイスが無い場合の情報表示
const setNoActiveDevice = (show) => {
  const el = document.getElementById('noActiveDeviceMessage');
  if (show) {
    if (!el.classList.contains('show')) el.classList.add('show');
  } else {
    if (el.classList.contains('show')) el.classList.remove('show');
  }
};

document.querySelector('#btnToHome').addEventListener('click', () => toHome());



